@tailwind base;
@tailwind components;
@tailwind utilities;

:root {
  --primary-color: #17a2b8;
  --dark-color: #343a40;
  --light-color: #f4f4f4;
  --danger-color: #dc3545;
  --success-color: #28a745;
  --secondary-color: #cc4c7d;
}

input {
  padding: 10px !important;
  border-radius: 10px;
}

select {
  padding: 10px !important;
  border-radius: 10px;
}

.row {
  box-sizing: border-box;
  opacity: 0.9;
  margin-left: 5rem;

  display: inline-flex;
}

.label {
  font-weight: 600;
  color: black;
  font-size: small;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.cert {
  display: block;
  margin-right: 100px;
  width: 10px;
}

body {
  font-family: "Raleway", sans-serif;
  font-size: 1rem;
  line-height: 1.6;
  background-color: #fff;
}

a {
  color: var(--primary-color);
  text-decoration: none;
}

ul {
  list-style: circle;
}

img {
  width: 20%;
}

.container {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 1rem;
  margin-top: 6rem;
  margin-bottom: 3rem;
}

.container2 {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  padding: 0 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  margin-left: 5rem;
}

.grid-1 {
  display: grid;
  grid-template-columns: repeat(1, 2fr);
  grid-gap: 0.1rem;
  margin: auto;
  padding: 2vh;
}

.grid-2 {
  display: grid;
  grid-template-columns: repeat(2, 0.4fr);
  grid-gap: 0.1rem;
  padding-left: 10vh;
}

.grid-3 {
  display: grid;
  grid-template-columns: repeat(3, 2fr);
  grid-gap: 0.5rem;
}

.grid-4 {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 0.25rem;
  border: 1px solid #ccc;
  padding-left: 4vh;
}

.grid-5 {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-gap: 0.1rem;
  border: 1px solid #ccc;
  padding-left: 4vh;
}

.grid-6 {
  display: grid;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 0.1rem;
  border: 1px solid #ccc;
  padding-left: 4vh;
}

.container3 {
  float: none;
  padding-right: 0.5pt;
}

.textarea2 {
  resize: none;

  padding: 0;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
  font-weight: bold;

  font-size: 12pt;
  color: #17202a;
}

.x-large {
  font-size: 4rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.large {
  font-size: 3rem;
  line-height: 1.2;
  margin-bottom: 1rem;
}

.lead {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.text-center {
  text-align: center;
}

.text-primary {
  color: var(--primary-color);
}

.text-danger {
  color: darkblue;
  text-align: center;
  font-size: 20pt;
}
.text-dark {
  color: var(--dark-color);
}

.p {
  padding: 0.5rem;
}
.p-1 {
  padding: 1rem;
}
.p-2 {
  padding: 2rem;
}
.p-3 {
  padding: 3rem;
}
.py {
  padding: 0.5rem 0;
}
.py-1 {
  padding: 1rem 0;
}
.py-2 {
  padding: 2rem 0;
}
.py-3 {
  padding: 3rem 0;
}

.m {
  margin: 0.5rem;
}
.m-1 {
  margin: 1rem;
}
.m-2 {
  margin: 2rem;
}
.m-3 {
  margin: 3rem;
}
.my {
  margin: 0.5rem 0;
}
.my-1 {
  margin: 1rem 0;
}
.my-2 {
  margin: 2rem 0;
}
.my-3 {
  margin: 3rem 0;
}
.my-5 {
  margin: 5rem 0;
}

.btn {
  display: inline-block;
  background: var(--secondary-color);
  color: #333;
  padding: 0.4rem 1.3rem;
  font-size: 1rem;
  border: none;
  cursor: pointer;
  margin-right: 0.5rem;
  transition: opacity 0.2s ease-in;
  outline: none;
}

.badge {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: center;
  margin: 0.3rem;
  background: var(--light-color);
  color: #333;
}

.badge2 {
  font-size: 0.8rem;
  padding: 0.1rem;
  text-align: left;
  margin: 0.3rem;
  background-color: cornsilk;
  color: darkorange;
}

.alert {
  padding: 0%;
  margin: 0%;
  width: auto;
  text-align: center;
  background: var(--light-color);
  color: #333;
}

.btn-primary,
.bg-primary,
.badge-primary,
.alert-primary {
  background: var(--primary-color);
  color: #fff;
}

.alert-secondary {
  background: var(--warning-color);
  color: rgb(105, 8, 16);
}

.btn-light,
.bg-light,
.badge-light,
.alert-light {
  background: var(--light-color);
  color: #333;
}

.btn-dark,
.bg-dark,
.badge-dark,
.alert-dark {
  background: var(--dark-color);
  color: #fff;
}

.small {
  color: #28a745;
  font-weight: bold;
}

.btn-danger,
.bg-danger,
.badge-danger,
.alert-danger {
  background: var(--danger-color);
  color: #fff;
}

.btn-success,
.bg-success,
.badge-success,
.alert-success {
  background: var(--success-color);
  color: #fff;
}

.btn-white,
.bg-white,
.badge-white,
.alert-white {
  background: #fff;
  color: #333;
  border: #ccc solid 1px;
}

.btn:hover {
  opacity: 0.8;
}

.bg-light,
.badge-light {
  border: #ccc solid 1px;
}

.round-img {
  border-radius: 50%;
}

.line {
  height: 100px;
  width: 500px;
  background: #ccc;
  margin: 1.5rem 0;
  font-size: 4rem;
}

.dark-overlay {
  background-color: hsl(0, 0%, 98%);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.form .form-group {
  margin: 0.5rem 0;
}

.form .form-text {
  display: block;
  margin-top: 0.5rem;
  color: darkgoldenrod;
  font-size: medium;
  font-weight: bold;
}

.form input[type="text"],
.form input[type="email"],
.form input[type="password"],
.form input[type="number"],
.form input[type="select"],
.form select,
.form textarea {
  display: inline-block block;
  width: 100%;
  padding: 0.4rem;
  font-size: 10pt;
  font-weight: bold;
  border: 1px solid #ccc;
  color: darkblue;
}

.form input[type="date"] {
  display: block;
  width: 90%;
  padding: 0.4rem;
  font-size: 0.8rem;
  border: 1px solid #ccc;
  color: rgb(231, 38, 13);
}

.form input[type="checkbox"] {
  zoom: 1.5;
}

.container22 {
  max-width: 1100px;
  margin: auto;
  overflow: hidden;
  align-items: center;
  background-color: "#FF6666";
  flex: 1;
}

.form input[type="submit"],
button {
  font: medium;
}

.form .social-input {
  display: flex;
}

.form .social-input i {
  padding: 0.5rem;
  width: 4rem;
}

.form .social-input i.fa-twitter {
  color: #38a1f3;
}
.form .social-input i.fa-facebook {
  color: #3b5998;
}
.form .social-input i.fa-instagram {
  color: #3f729b;
}
.form .social-input i.fa-youtube {
  color: #c4302b;
}
.form .social-input i.fa-linkedin {
  color: #0077b5;
}

.table th,
.table td {
  padding: 1rem;
  text-align: left;
}

.table th {
  background: var(--light-color);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.7rem 2rem;
  position: fixed;
  z-index: 1;
  width: 100%;
  top: 0;

  /* border-bottom: solid 1px var(--primary-color); */
  opacity: 0.9;
  background-color: rgb(118, 99, 114);
}

.navbar ul {
  display: flex;
}

.navbar a {
  color: white;
  padding: 0.45rem;
  margin: 0 0.25rem;
}

.navbar a:hover {
  color: thistle;
}

.navbar .welcome span {
  margin-right: 0.6rem;
}

.landing {
  position: relative;

  height: 100vh;
}

.landing-inner {
  height: 100%;
  width: 50%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.landing-inner2 {
  color: rgba(42, 0, 139, 0.815);
  height: 100%;
  width: 80%;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: left;
}

.profile {
  display: grid;
  grid-template-columns: 2fr 2fr 5fr;
  align-items: center;
  grid-gap: 0.5rem;
  padding: 0.2rem;
  line-height: 0.5;
  margin-bottom: 0.2rem;
  border-bottom: solid 2px var(--primary-color);
  border-top: solid 2px var(--primary-color);
  border-left: solid 2px var(--primary-color);
  border-right: solid 2px var(--primary-color);
}

.profile2 {
  display: grid;
  grid-template-columns: 2fr 2fr 1fr 2fr 2fr;
  align-items: center;
  grid-gap: 0.5rem;
  padding: 0.25rem;
  margin-left: 10vh;

  line-height: 0.8;
  margin-bottom: 0.5rem;

  font-weight: bold;

  border-bottom: solid 1px var(--primary-color);
  border-top: solid 1px var(--primary-color);
  border-left: solid 1px var(--primary-color);
  border-right: solid 1px var(--primary-color);
}

.odd {
  background-color: palegreen;
  color: green;
  font-weight: bold;
}

.but-center {
  display: flex;
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.even {
  background-color: mistyrose;
  color: crimson;
}

.profile3 {
  display: grid;
  grid-template-columns: 3fr 2fr 4fr;
  align-items: center;
  grid-gap: 0.7rem;
  padding: 0.7rem;

  margin-left: 10vh;
  line-height: 0.8;
  margin-bottom: 0.5rem;

  font-weight: bold;

  border-bottom: solid 1px var(--primary-color);
  border-top: solid 1px var(--primary-color);
  border-left: solid 1px var(--primary-color);
  border-right: solid 1px var(--primary-color);
}

.profile-grid {
  display: grid;
  grid-template-areas:
    "top top"
    "about about"
    "exp edu"
    "github github";
  grid-gap: 1rem;
  padding: 1rem;

  border-bottom: solid 2px var(--primary-color);
  border-top: solid 2px var(--primary-color);
  border-left: solid 2px var(--primary-color);
  border-right: solid 2px var(--primary-color);
}

.profile-top {
  grid-area: top;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.profile-top img {
  width: 250px;
}

.profile-top .icons a {
  color: #fff;
  margin: 0 0.3rem;
}

.profile-top .icons a:hover {
  color: var(--dark-color);
}

.profile-about {
  grid-area: about;
  text-align: center;
}

.profile-about .skills {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.profile-exp {
  grid-area: exp;
}

.profile-edu {
  grid-area: edu;
}

.profile-exp h2,
.profile-edu h2 {
  margin-bottom: 1rem;
}

.profile-exp > div,
.profile-edu > div {
  margin-bottom: 1rem;
  padding-bottom: 1rem;
  border-bottom: #ccc 1px dotted;
}

.profile-exp > div:last-child,
.profile-edu > div:last-child {
  border: 0;
}

.profile-exp p,
.profile-edu p {
  margin: 0.5rem 0;
}

.profile-github {
  grid-area: github;
}

.profile-github .repo {
  display: flex;
}

.profile-github .repo > div:first-child {
  flex: 7;
  flex-basis: 70%;
}

.profile-github > div:last-child {
  flex: 3;
  flex-basis: 20%;
}

.post-form .post-form-header {
  background: var(--primary-color);
  padding: 0.5rem;
}

.post {
  display: grid;
  grid-template-columns: 1fr 4fr;
  grid-gap: 2rem;
  align-items: center;
}

.post > div:first-child {
  text-align: center;
}

.post img {
  width: 100px;
}

.post .comment-count {
  background: var(--light-color);
  color: var(--primary-color);
  padding: 0.1rem 0.2rem;
  border-radius: 5px;
  font-size: 0.8rem;
}

.post .post-date {
  color: #aaa;
  font-size: 0.8rem;
  margin-bottom: 0.5rem;
}

@media (max-width: 700px) {
  .container {
    margin-top: 8rem;
  }

  .hide-sm {
    display: none;
  }

  .x-large {
    font-size: 3rem;
  }

  .large {
    font-size: 2rem;
  }

  .lead {
    font-size: 1rem;
  }

  .navbar {
    display: block;
    text-align: center;
  }

  .navbar ul {
    text-align: center;
    justify-content: center;
  }

  .navbar h1 {
    margin-bottom: 1rem;
  }

  .navbar .welcome {
    display: none;
  }

  .profile {
    grid-template-columns: 1fr;
    text-align: center;
  }

  .profile ul {
    display: none;
  }

  .profile-top img,
  .profile img {
    width: 200px;
    margin: auto;
  }

  .profile-grid {
    grid-template-areas:
      "top"
      "about"
      "exp"
      "edu"
      "github";
  }

  .profile-about .skills {
    flex-direction: column;
  }

  .dash-buttons a {
    display: block;
    width: 100%;
    margin-bottom: 0.2rem;
  }

  .post {
    grid-template-columns: 1fr;
  }

  .post a,
  .post button {
    padding: 0.3rem 0.4rem;
  }
}
